.atr_picture-bg {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  z-index: -1;
}
.atr_picture-bg img {
  height: auto;
  width: 100%;
  object-fit: cover;
}

.atr_button {
  background-color: #d3e3f3;
  border: none;
  border-radius: 2px;
  color: #255891;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: 2px;
  margin-top: 40px;
  padding: 6px 40px;
  text-align: center;
  transition: background-color 0.2s ease;
}
.atr_button:hover {
  background-color: #f5f5f5;
}

.atr_h2 {
  color: #f5f5f5;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 0;
  text-align: center;
  font-size: 80px;
  line-height: 1.1;
}
.atr_h2 span {
  display: block;
}
.atr_h2 .atr_h-small {
  color: #f5f5f5;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 0;
  text-align: center;
  font-size: 48px;
  line-height: 1.5;
}

.atr_offleft {
  position: relative;
  right: 120px;
}

@media only screen and (max-width: 992px) {
  .atr_h2 {
    font-size: 70px;
  }
  .atr_h2 .atr_h-small {
    font-size: 42px;
  }

  .atr_offleft {
    right: 100px;
  }

  .atr_button {
    font-size: 12px;
    padding: 6px 30px;
  }
}
@media only screen and (max-width: 768px) {
  .atr_h2 {
    font-size: 60px;
  }
  .atr_h2 .atr_h-small {
    font-size: 36px;
  }

  .atr_offleft {
    right: 80px;
  }
}
@media only screen and (max-width: 576px) {
  .atr_h2 {
    font-size: 42px;
  }
  .atr_h2 .atr_h-small {
    font-size: 24px;
  }

  .atr_offleft {
    right: 60px;
  }
}
html {
  scroll-behavior: smooth;
}

.atr_zip-code-container {
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 72vh;
  overflow: hidden;
  position: relative;
}
.atr_zip-code-container.force-full-height {
  height: calc(var(--vh, 1vh) * 100);
}
.atr_zip-code-container h1 {
  color: #fff;
  font-size: 60px;
  text-align: center;
  margin: 0 10% 60px 10%;
}
.atr_zip-code-container form {
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
}
.atr_zip-code-container .atr_zip-code-input {
  border: 2px solid transparent;
  border-bottom: 2px solid #d3e3f3;
}
.atr_zip-code-container .atr_zip-code-input:focus-within {
  border: 2px solid #d3e3f3;
  border-radius: 4px;
}
.atr_zip-code-container .atr_zip-code-input img {
  margin-bottom: 40px;
  margin-left: 40px;
}
.atr_zip-code-container .atr_zip-code-input .atr_sm-search-icon {
  display: none;
}
.atr_zip-code-container input {
  background-color: transparent;
  border: none;
  color: #f5f5f5;
  font-size: 60px;
  margin-right: 20px;
  text-align: center;
  width: 400px;
}
.atr_zip-code-container input:focus {
  outline: none;
}
.atr_zip-code-container input::-webkit-input-placeholder {
  color: #f5f5f5;
  opacity: 1;
}
.atr_zip-code-container input:-ms-input-placeholder {
  color: #f5f5f5;
  opacity: 1;
}
.atr_zip-code-container input::placeholder {
  color: #f5f5f5;
  opacity: 1;
}
.atr_zip-code-container input:focus::-webkit-input-placeholder {
  color: transparent;
}
.atr_zip-code-container input:focus:-ms-input-placeholder {
  color: transparent;
}
.atr_zip-code-container input:focus::placeholder {
  color: transparent;
}

@media only screen and (max-width: 1200px) {
  .atr_zip-code-container .atr_zip-code-input img {
    margin-bottom: 36px;
    margin-left: 36px;
  }
  .atr_zip-code-container h1 {
    font-size: 50px;
  }
  .atr_zip-code-container input {
    font-size: 50px;
    margin-right: 16px;
    width: 380px;
  }
}
@media only screen and (max-width: 992px) {
  .atr_zip-code-container .atr_zip-code-input img {
    margin-bottom: 30px;
    margin-left: 30px;
  }
  .atr_zip-code-container input {
    margin-right: 12px;
    width: 350px;
  }
}
@media only screen and (max-width: 768px) {
  .atr_zip-code-container .atr_zip-code-input img {
    margin-bottom: 26px;
    margin-left: 26px;
  }
  .atr_zip-code-container input {
    font-size: 48px;
    margin-right: 10px;
    width: 300px;
  }
}
@media only screen and (max-width: 576px) {
  .atr_zip-code-container .atr_zip-code-input img {
    margin-bottom: 22px;
    margin-left: 22px;
  }
  .atr_zip-code-container .atr_zip-code-input .atr_sm-search-icon {
    display: inline;
  }
  .atr_zip-code-container .atr_zip-code-input .atr_lg-search-icon {
    display: none;
  }
  .atr_zip-code-container h1 {
    font-size: 42px;
  }
  .atr_zip-code-container input {
    font-size: 42px;
    font-weight: 300;
    margin-right: 8px;
    width: 220px;
  }
}